.App {
  text-align: center;
  background-color: white;
  font-family:monospace;
}

.App-logo {
  height: 390px;
}

.App-header {
  height: 320px;
  color:  black;
  float: left;
  font-weight:30em;
  background-color: white;
  margin-top: -5px;
}
.Profile-Card{
   -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
           box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
   max-width: 320px;
   margin: auto;
   text-align: center;
   padding-left:none;
   background-color: #4A235A;
   color: #F1C40F;
}

.p Title{
  color:  white;
  font-size: 15px;
}

.Pofile-Card a{
   font-size: 10px;
   -webkit-text-decoration-color:#F1C40F;
           text-decoration-color:#F1C40F;
}

.App-title {
  font-size: 20px;
  }

.App-intro {
  font-size: large;
  color: #F1C40F;
  padding-top: 30px;
  font-weight: bold;
}

.App-Main{
    color: black;
    text-align: left;
}

.App-Main-Tab{
  overflow: hidden;
  color:  black;
  font-family: monospace;
  float:none;
  padding-right: 20px;
  background-color: inherit;
}

.App-Main-Tab button{
   float: center;
   border: none;
   outline: none;
   cursor: pointer;
   padding: 14px 16px;
   -webkit-transition: 0.3s;
   -o-transition: 0.3s;
   transition: 0.3s;
   color:white;
   font-weight: bold;
   font-family: monospace;
   background-color:#4A235A;
   font-size: 13px;
}

.App-Main-Tab button:hover{
   background-color: #F1C40F;
}

.App-Main-Tab button:active {
    background-color: #F1C40F;
}

.Tab-content {
    display: none;
    color: black;
    text-align: left;
    font-size: 15px;
    float:left;
    background-color: white;
    padding-left: 16px;
}

a:link, a:visited, a:active {
  color:#F1C40F;
  text-decoration: none;
  text-align: center;
  padding-right: 10px;
}

a:hover{
  color: #58D68D;
  text-decoration: none;
  text-align: center;
}

li{
  padding-bottom: 10px;
  width: 700px;
  list-style: none;
  padding-left: 10px;
}

b{
  background-color:#D7BDE2;
}

i{
    border: solid black;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
}

.Arrow-Right{
  -ms-transform: rotate(-45deg);
      transform: rotate(-45deg);
   -webkit-transform: rotate(-45deg);
}

.App-Footer{
    color:white;
    padding-top: 100px;
    padding-bottom: none;
}



